import { Component, Input, OnDestroy, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { head, isEqual } from 'lodash';
import { combineLatest, Observable, of, ReplaySubject } from 'rxjs';
import { distinctUntilChanged, filter, map, shareReplay, skip, takeUntil } from 'rxjs/operators';
import { IDataPageResult } from '@helix/platform/shared/api';
import { RxWizardModalComponent } from '@helix/platform/shared/components';
import { IRecordDefinition, RX_RECORD_DEFINITION } from '@helix/platform/record/api';
import { IRowDataItem } from '@helix/platform/view/api';
import {
  IRecordGridColumn,
  IRecordGridConfig,
  RecordGridComponent,
  RowSelectionMode
} from '@helix/platform/view/components';
import { AX_BUNDLE_DETAILS } from '../../bundle-details/bundle-details.constant';
import { RxExternalDataService } from '../../../services/external-data/external-data.service';
import { IExternalRecordWizardContext } from '../external-record-wizard.types';

@Component({
  selector: 'ax-web-api-selection-wizard-step',
  templateUrl: 'web-api-selection-step.component.html',
  styleUrls: ['./web-api-selection-step.component.scss']
})
export class WebApiSelectionStepComponent implements OnInit, OnDestroy {
  @Input()
  context: IExternalRecordWizardContext;

  @ViewChild(RecordGridComponent, { static: true })
  recordGrid: RecordGridComponent;

  @ViewChild('webApiNameCellTemplate', { static: true })
  webApiNameCellTemplate: TemplateRef<any>;

  sectionLabel: string;
  recordGridConfig$: Observable<IRecordGridConfig>;

  private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);

  constructor(
    private rxExternalDataService: RxExternalDataService,
    private rxWizardModalComponent: RxWizardModalComponent,
    private translateService: TranslateService
  ) {}

  ngOnInit(): void {
    this.sectionLabel = this.translateService.instant(
      'com.bmc.arsys.rx.innovation-studio.external-record-wizard.web-api-selection.label'
    );

    const webApiSelectionStepContext$ = this.rxWizardModalComponent.context$.pipe(
      shareReplay(1),
      takeUntil(this.destroyed$)
    );

    webApiSelectionStepContext$
      .pipe(
        map((stepContext: IExternalRecordWizardContext) => stepContext.dataSourceName),
        distinctUntilChanged(isEqual),
        skip(1)
      )
      .subscribe(() => {
        this.rxWizardModalComponent.api.updateContext({ webApi: null });
        this.recordGrid.api.refresh().subscribe();
      });

    const isCurrentStep$ = combineLatest([
      this.rxWizardModalComponent.steps$,
      this.rxWizardModalComponent.tabIndex$
    ]).pipe(
      map(([steps, tabIndex]) => steps[tabIndex].id === 'web-api-selection'),
      takeUntil(this.destroyed$)
    );

    const changedWebApi$ = webApiSelectionStepContext$.pipe(
      map((stepContext: IExternalRecordWizardContext) => stepContext.webApi),
      distinctUntilChanged(isEqual)
    );

    combineLatest([isCurrentStep$, changedWebApi$])
      .pipe(
        filter(([isCurrentStep, changedWebApi]) => isCurrentStep),
        distinctUntilChanged(isEqual)
      )
      .subscribe(([isCurrentStep, changedWebApi]) => {
        if (changedWebApi) {
          this.rxWizardModalComponent.api.enableNextButton();
        } else {
          this.rxWizardModalComponent.api.disableNextButton();
        }
      });

    const gridColumns: IRecordGridColumn[] = [
      {
        fieldId: AX_BUNDLE_DETAILS.definitionGridColumns.name.fieldId,
        title: this.translateService.instant('com.bmc.arsys.rx.client.common.name.label'),
        cellTemplate: this.webApiNameCellTemplate
      }
    ];

    const gridRecordDefinition: IRecordDefinition = {
      fieldDefinitions: [
        {
          id: AX_BUNDLE_DETAILS.definitionGridColumns.name.fieldId,
          resourceType: RX_RECORD_DEFINITION.resourceTypes.character
        }
      ]
    };

    this.recordGridConfig$ = of({
      columns: gridColumns,
      emptyStateLabelText: this.translateService.instant(
        'com.bmc.arsys.rx.innovation-studio.external-record-wizard.web-api-selection.grid.empty-state.label'
      ),
      enableRowSelection: RowSelectionMode.Single,
      enableFiltering: false,
      recordIdField: AX_BUNDLE_DETAILS.definitionGridColumns.name.fieldId,
      styles: 'flex-fill',
      useExternalFiltering: false,
      getRecordDefinition: () => of(gridRecordDefinition),
      getData: (): Observable<IDataPageResult> =>
        this.context.dataSourceName
          ? this.rxExternalDataService.getWebApiDefinitions()
          : of({
              totalSize: 0,
              data: []
            })
    });

    this.recordGrid.rowSelectionChanged
      .pipe(skip(2), takeUntil(this.destroyed$))
      .subscribe((selectedRows: IRowDataItem[]) => {
        this.rxWizardModalComponent.api.updateContext({
          webApi: head(selectedRows) || null
        });
      });
  }

  ngOnDestroy() {
    this.destroyed$.next();
    this.destroyed$.complete();
  }
}
